import {useState, useEffect} from 'react';
import {Container,Row,Col} from 'react-bootstrap';
import myimage from '../assets/img/myimage.jpg';
import earth from "../assets/img/Earth.png";
import "animate.css";
import React from 'react';

export const Banner = () =>{
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ['Web Developer', 'Software Engineer'] ;
    const [text, setText] = useState('');
    const [delta,setDelta ] = useState(300 - Math.random()*100);
    const period = 2000;

    useEffect(()=>{
        let ticker = setInterval( ()=>{
            tick();
        },delta)
        return ()=>{clearInterval(ticker)};
    },[text])


    const tick = () =>{
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length -1) : fullText.substring(0,text.length +1);
        setText(updatedText);
        if(isDeleting){
            setDelta(prevDelta => prevDelta / 2)
        }

        if(!isDeleting && updatedText === fullText){
            setIsDeleting(true);
            setDelta(period);
        }else if(isDeleting && updatedText === ''){
            setIsDeleting(false);
            setLoopNum(loopNum+1);
            setDelta(500);
        }
    }

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={5}>
                        <img src={earth} className="earth-shadow" alt="Header Img"/>
                    </Col>
                    <Col xs={12} md={6} xl={7}>
                        <img src= {myimage} className="my-picture" alt="My pi"/>
                        <div className = "animate__animated animate__bounce animate_infinite">
                            <span className="tagLine">Welcome to my Portfolio</span>
                            <h1>{`Hi I am a `}<span className="wrap">{text}</span></h1>
                            <p>I am a graduated computer science student. Currently, I am learning to become a full stack developer</p>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}